/** Reset **/
* {
  padding: 0;
  margin: 0;
  border: 0;
  list-style: none;
  outline: none;
  box-sizing: border-box;
}
:root {
  /**  Sun  **/
  --sky-bg: #96dcee;
  --sun-bg: #fffaa8;
  /**  Moon  **/
  --sky-night-bg: #6b7abb;
  --moon-dots: #e8e8ea;

  /**  Elements  **/
  --white: hsl(0, 0%, 100%);
  --bg: hsl(212, 45%, 89%);
  --blue: hsl(220, 15%, 55%);
  --dark-blue: hsl(218, 44%, 22%);
  --rgba-darkblue: rgba(31, 50, 81, 0.35);
  --red: #ba0035;

  --shadow: 0 0.3rem 1rem var(--rgba-darkblue);
  --cubic-bezier: 0.8s cubic-bezier(0.06, 0.8, 0.1, 1);
}
:root body.dark {
  --white: #002625;
  --bg: #12121a;
  --blue: #fff;
  --dark-blue: #00beb7;
}

::-webkit-scrollbar {
  width: 0.7rem;
}
::-webkit-scrollbar-track {
  background: transparent;
}
::-webkit-scrollbar-thumb {
  border-radius: 1rem;
}
html {
  scrollbar-width: thin;
  scroll-behavior: smooth;
  background-color: var(--bg);
}

body {
  display: grid;
  place-items: center;
  min-height: 100vh;
  padding: 1rem;
  font-family: "Sulphur Point", sans-serif;
  font-size: 1.2rem;
  background: #8f8f8f;
  color: #3b3b3b;
  transition: var(--cubic-bezier);
}
header {
  display: grid;
  place-items: center;
  place-content: center;
  grid-template-columns: 4rem max-content 4rem;
  gap: 1rem;
  padding-inline: 1rem;
  padding-bottom: 5rem;
}
main {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 5rem;
}

h1 {
  margin-bottom: 1.5rem;
}
h1,
h2,
a {
  color: #3b3b3b;
}
.relative {
  position: relative;
}
.word-wrap {
  word-wrap: anywhere;
}
.sr-only {
  width: 1px;
  height: 1px;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
}

/**  Toggle Theme  **/
.toggle-checkbox {
  display: none !important;
}
.toggle-checkbox:checked + .toggle-label {
  background: var(--sky-night-bg);
}
.toggle-checkbox:checked + .toggle-label .toggle-label-bg {
  left: 3rem;
  width: 0.6rem;
  height: 0.5rem;
}
.toggle-checkbox:checked + .toggle-label .toggle-label-bg:before,
.toggle-checkbox:checked + .toggle-label .toggle-label-bg:after {
  width: 0.5rem;
  height: 0.5rem;
  top: -1rem;
}
.toggle-checkbox:checked + .toggle-label .toggle-label-bg:after {
  left: -1.5rem;
  top: 1rem;
}
.toggle-checkbox:checked + .toggle-label:before {
  background: var(--white);
  animation-name: switch;
  animation-duration: 0.4s;
  animation-fill-mode: forwards;
}
.toggle-checkbox:checked + .toggle-label:after {
  transition-delay: 0.4s;
  opacity: 1;
}
.toggle-label {
  position: relative;
  display: flex;
  width: 8rem;
  height: 3.8rem;
  border-radius: 2rem;
  background: var(--sky-bg);
  transition: all var(--cubic-bezier);
}
.toggle-label-bg {
  position: relative;
  left: 5.5rem;
  top: 1.8rem;
  width: 0.5rem;
  height: 0.3rem;
  border-radius: 0.5rem;
  background: var(--white);
  transition: all var(--cubic-bezier);
}
.toggle-label-bg:before,
.toggle-label-bg:after {
  content: "";
  position: absolute;
  top: 0.55rem;
  left: -0.4rem;
  width: 2rem;
  height: 0.3rem;
  border-radius: 0.5rem;
  background: var(--white);
  transition: all var(--cubic-bezier);
}
.toggle-label-bg:after {
  top: -0.6rem;
  left: -1.5rem;
}
.toggle-label:before {
  content: "";
  position: absolute;
  width: 3rem;
  height: 3rem;
  top: 0.4rem;
  left: 0.3rem;
  border-radius: 3rem;
  background: var(--sun-bg);
  animation-name: switchReverse;
  animation-duration: 0.4s;
  animation-fill-mode: forwards;
  transition: all var(--cubic-bezier);
}
.toggle-label:after {
  content: "";
  position: absolute;
  right: 2rem;
  top: 0.4rem;
  width: 0.5rem;
  height: 0.5rem;
  border-radius: 50%;
  opacity: 0;
  background: transparent;
  box-shadow: var(--moon-dots) 0.5rem 0.5rem 0 0.08rem,
    var(--moon-dots) -0.5rem 1.2rem 0 -0.08rem;
  transition-delay: 0;
  transition: all var(--cubic-bezier);
}

/**  Form & Input  **/
form {
  width: 100%;
  position: relative;
}
select {
  width: 100%;
  padding: 1.2rem;
  border-radius: 0.8rem;
  background-color: #ffffff;
  color: #000000; /* Ensure text color is light for contrast */
  box-shadow: var(--shadow);
  -webkit-appearance: none;  /* Removes default styling */
  appearance: none;  /* Removes default styling */
}

/* Styling options for greater compatibility */
option {
  width: 100%;
  padding: 3rem;
  border-radius: 0.8rem;
  font-size: 22px;
}

option:hover {
  background-color: yellow;
}

option:before {
  content: ">";
  font-size: 20px;
  display: none;
  padding-right: 10px;
  padding-left: 5px;
  color: #fff;
}

option:hover:before {
  display: inline;
}

/* Add a custom arrow indicator for the select element */
select {
  background-image: url('data:image/svg+xml;charset=US-ASCII,<svg%20width%3D"20"%20height%3D"20"%20viewBox%3D"0%200%2020%2020"%20fill%3D""%20xmlns%3D"http%3A//www.w3.org/2000/svg"><path%20d%3D"M4%205L10%2011L16%205"%20stroke%3D"black"%20stroke-width%3D"2"%20stroke-linecap%3D"round"%20stroke-linejoin%3D"round"/></svg>');
  background-repeat: no-repeat;
  background-position: right 1rem center;
  background-size: 1.2rem;
}

/* Focus and hover styles for better user feedback */
select:focus, select:hover {
  opacity: 0.85;
  transform: scale(1.1);
}

input,
button {
  display: block;
  width: 100%;
  padding: 1rem;
  border-radius: 0.8rem;
  margin-bottom: 1.5rem;
  box-shadow: var(--shadow);
  transition: var(--cubic-bezier);
}
.clear-btn {
  position: absolute;
  top: 0.8rem;
  right: 0;
  width: max-content;
  font-size: 1.8rem;
  background: transparent;
  box-shadow: none;
  z-index: 95;
}
input[type="file"] {
  background-color: var(--white);
  padding-block: 0.5rem;
}
input[type="checkbox"] {
  width: max-content;
  display: inline-block;
  margin-right: 1rem;
}
input[type="button"] {
  display: inline;
  padding: 0.3rem;
  width: 4rem;
  height: 2rem;
  margin-right: 1rem;
}
label {
  display: inline;
}
.error-msg {
  color: var(--red);
  font-weight: 700;
  padding-left: 1rem;
}
.success-msg {
  color: #008000;
  font-weight: 700;
  padding-top: 1.5rem;
  text-align: center;
}
button[type="submit"] {
  display: flex;
  justify-content: center;
  gap: 1rem;
  width: 100%;
  margin-block: 2.5rem 0;
  font-size: 1.25rem;
  font-weight: 700;
  text-transform: uppercase;
  color: var(--white);
  background-color: var(--dark-blue);
}
button[type="submit"] > img {
  max-width: 1.5rem;
  animation: pulse infinite 1s alternate;
}
button:hover,
input:focus,
input:active {
  opacity: 0.85;
  transform: scale(1.1);
}
.sketch-picker {
  position: absolute !important;
  z-index: 9;
}

/**  Card  **/
.card-white {
  max-width: 26rem;
  /* padding: 1.2rem; */
  margin-top: 1rem;
  border-radius: 1.5rem;
  text-align: center;
  background-color: #000000;
  box-shadow: var(--shadow);
}
.card-black{
  max-width: 26rem;
  /* padding: 1.2rem; */
  margin-top: 1rem;
  border-radius: 1.5rem;
  text-align: center;
  background-color: #ffffff;
  box-shadow: var(--shadow);
}
/* .card > * {
  margin-bottom: 1.5rem;
} */
.card .qr-box {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 20rem;
  border-radius: 0.8rem;
  overflow: hidden;
}
/* .card .qr-box::before,
.card .qr-box::after {
  content: "";
  position: absolute;
  top: -12rem;
  right: 6rem;
  width: 22rem;
  height: 22rem;
  border-radius: 100%;
  background-color: rgba(255, 255, 255, 0.08);
  z-index: 99;
  display: none;
} */
.card .qr-box::after {
  top: 14rem;
  right: -8rem;
  width: 20rem;
}
.card .qr-box canvas {
  border-radius: 0.8rem;
  transition: var(--cubic-bezier);
  z-index: 98;
}

/**  Responsive  **/
@media (max-width: 50em) {
  header {
    padding-top: 3rem;
  }
  main {
    padding-bottom: 7rem;
    grid-template-columns: 1fr;
  }
}

/**  Animations  **/
@keyframes pulse {
  from {
    transform: scale(0.8);
  }
  to {
    transform: scale(1.2);
  }
}

@keyframes switch {
  0% {
    left: 0.5rem;
  }
  60% {
    left: 0.5rem;
    width: 5rem;
  }
  100% {
    left: 4.5rem;
    width: 3rem;
  }
}
@keyframes switchReverse {
  0% {
    left: 2rem;
    width: 3rem;
  }
  60% {
    left: 1rem;
    width: 5rem;
  }
  100% {
    left: 0.5rem;
  }
}

/**  Footer  **/
.attribution {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  padding: 10px;
  margin-top: 30px;
  text-align: center;
  background-color: #8f8f8f;
  z-index: 1000;
  
}
/**  button  **/
.DownloadButton {
  display: flex;
  justify-content: center;
  gap: 1rem;
  width: 50%;
  margin-block: 2rem 0;
  font-size: 1.25rem;
  font-weight: 700;
  text-transform: uppercase;
  color: var(--white);
  background-color: #20c997;
}
.CoppyButton {
  display: flex;
  justify-content: center;
  gap: 1rem;
  width: 50%;
  margin-block: 2rem 0;
  font-size: 1.25rem;
  font-weight: 700;
  text-transform: uppercase;
  color: var(--white);
  background-color: var(--dark-blue);
}

.actions {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 30px;
  margin-bottom: 50px;
}